/**
 * @enum
 */
var AccountStatus = {

  /**
   * Available, but not yet requested.
   */
  AVAIL: "AVAIL",

  /**
   * Requested, but not yet available.
   */
  PEND: "PEND",

  /**
   * Active.
   */
  ACTIVE: "ACTIVE",

};


module.exports = AccountStatus;
